.uui-typography {
  font-family: var(--uui-font);
  color: var(--uui-text-primary);
}
.uui-typography .hero-header, .uui-typography h1, .uui-typography h2, .uui-typography h3, .uui-typography h4, .uui-typography h5, .uui-typography h6 {
  margin: 0;
}
.uui-typography .hero-header {
  font-family: var(--uui-font);
  font-weight: 500;
  margin: 30px 0;
  font-size: 66px;
  line-height: 72px;
}
.uui-typography .promo-header, .uui-typography h1, .uui-typography h2, .uui-typography h3 {
  font-family: var(--uui-font);
  font-weight: 700;
}
.uui-typography h4, .uui-typography h5, .uui-typography h6 {
  font-family: var(--uui-font);
  font-weight: 600;
}
.uui-typography h1, .uui-typography h2, .uui-typography h3, .uui-typography h4, .uui-typography h5, .uui-typography h6 {
  margin-bottom: 0.5em;
  margin-top: 1.2em;
  line-height: 1.35em;
}
.uui-typography h1 {
  font-size: 42px;
}
.uui-typography h2 {
  font-size: 30px;
  letter-spacing: 0.1px;
}
.uui-typography h2 .promo-header {
  letter-spacing: 2px;
}
.uui-typography h3 {
  font-size: 24px;
}
.uui-typography h4 {
  font-size: 18px;
  letter-spacing: 0.1px;
}
.uui-typography h5 {
  font-size: 16px;
}
.uui-typography h6 {
  font-size: 14px;
}
.uui-typography b, .uui-typography strong {
  font-family: var(--uui-font);
  font-weight: 600;
}
.uui-typography i, .uui-typography em {
  font-family: var(--uui-font);
  font-weight: 400;
  font-style: italic;
}
.uui-typography a {
  display: inline-flex;
  align-items: center;
  color: var(--uui-link);
  text-decoration: underline;
}
.uui-typography a:focus-visible {
  outline: var(--uui-focus-outline-width) solid var(--uui-outline-focus);
  outline-offset: 2px;
  border-radius: 2px;
}
.uui-typography a:visited {
  color: var(--uui-link-visited);
}
.uui-typography a:visited:hover {
  color: var(--uui-link-visited-hover);
}
.uui-typography a:hover {
  color: var(--uui-link-hover);
}
.uui-typography code {
  padding: 0.125em 0.25em;
  color: var(--uui-text-primary);
  background-color: var(--uui-neutral-30);
  font-family: var(--uui-font-mono, monospace);
  font-weight: 400;
  white-space: pre-wrap;
  word-wrap: normal;
}
.uui-typography ol, .uui-typography ul {
  padding: 0;
}
.uui-typography li {
  margin-left: 1.25em;
  line-height: 1.5;
}
.uui-typography pre {
  padding: 1.25em;
  font-family: monospace;
  white-space: pre-wrap;
  word-wrap: normal;
}
.uui-typography img {
  max-width: 100%;
}
.uui-typography figure {
  margin: 0;
}
.uui-typography figure figcaption {
  font-size: 0.9em;
  opacity: 0.9;
  text-align: center;
}
.uui-typography p {
  margin: 0.5em 0;
  line-height: 1.5;
}
.uui-typography .uui-critical {
  color: var(--uui-text-critical);
}
.uui-typography .uui-info {
  color: var(--uui-text-info);
}
.uui-typography .uui-success {
  color: var(--uui-text-success);
}
.uui-typography .uui-warning {
  color: var(--uui-text-warning);
}
.uui-typography .uui-highlight {
  background-color: var(--uui-warning-10);
}

.uui-typography-size-12 {
  font-size: 12px;
}

.uui-typography-size-14 {
  font-size: 14px;
}

.uui-typography-size-16 {
  font-size: 16px;
}

.root {
  --uui-dtp_body-title-bg: var(--uui-control-bg);
  --uui-dtp_body-item-bg-selected: var(--uui-primary-50);
  --uui-dtp_body-item-border-hover: var(--uui-primary-50);
  --uui-dtp_body-text: var(--uui-control-text);
  --uui-dtp_body-text-selected: var(--uui-neutral-0);
  --uui-dtp_body-content-font: var(--uui-font);
  --uui-dtp_body-content-font-weight: 400;
}

:global(.uui-datepickerBody-wrapper) {
  min-height: 300px;
  padding: 0 18px 12px;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-datepicker-container) {
  background-color: var(--uui-dtp_body-title-bg);
}
:global(.uui-datepickerBody-wrapper) :global(.uui-datepickerheader-header) {
  padding-top: 18px;
  padding-bottom: 6px;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-content), :global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-content) {
  font-family: var(--uui-dtp_body-content-font);
  font-weight: var(--uui-dtp_body-content-font-weight, normal);
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-month-container) {
  margin: 0 0 12px;
  display: flex;
  flex-direction: column;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-month-container) > * {
  display: flex;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year-container) {
  display: flex;
  flex-direction: column;
  margin: 0 0 12px;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year-container) > * {
  display: flex;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year-row) {
  display: flex;
  justify-content: space-between;
  width: 252px;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-month) {
  width: 72px;
  height: 36px;
  border-radius: 20px;
  margin: 6px;
  color: var(--uui-dtp_body-text);
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-month):hover, :global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-month):focus {
  outline: none;
  width: 70px;
  height: 34px;
  border: 1px solid var(--uui-dtp_body-item-border-hover);
  cursor: pointer;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year) {
  width: 63px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  margin: 6px 0;
  color: var(--uui-dtp_body-text);
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year):hover, :global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-year):focus {
  outline: none;
  width: 61px;
  height: 34px;
  border: 1px solid var(--uui-dtp_body-item-border-hover);
  cursor: pointer;
}
:global(.uui-datepickerBody-wrapper) :global(.uui-monthselection-current-month):global(.uui-monthselection-month) {
  background-color: var(--uui-dtp_body-item-bg-selected);
  color: var(--uui-dtp_body-text-selected);
}
:global(.uui-datepickerBody-wrapper) :global(.uui-yearselection-current-year):global(.uui-yearselection-year) {
  background-color: var(--uui-dtp_body-item-bg-selected);
  color: var(--uui-dtp_body-text-selected);
}